/* JUMP */
h1 {
  cursor: default;
  position: relative;
  top: 0;
  width: 100%;
  height: 100px;
  margin: auto;
  display: block;
  text-align: center;
  font-family: "Luckiest Guy", cursive;
}

h1 span {
  font-family: "Jua", sans-serif;
  position: relative;
  top: 20px;
  display: inline-block;
  -webkit-animation: bounce 0.3s ease infinite alternate;
          animation: bounce 0.3s ease infinite alternate;
  font-size: 80px;
  color: #fd7468;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
    0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent,
    0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
}

h1 span:nth-child(2) {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

h1 span:nth-child(3) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

h1 span:nth-child(4) {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

h1 span:nth-child(5) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

h1 span:nth-child(6) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

h1 span:nth-child(7) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

h1 span:nth-child(8) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

h1 span:nth-child(9) {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

h1 span:nth-child(10) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

h1 span:nth-child(11) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

h1 span:nth-child(12) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

h1 span:nth-child(13) {
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

h1 span:nth-child(14) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

/* ANIMATION */
@-webkit-keyframes bounce {
  100% {
    top: -20px;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
      0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc,
      0 50px 25px rgba(0, 0, 0, 0.2);
  }
}
@keyframes bounce {
  100% {
    top: -20px;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
      0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc,
      0 50px 25px rgba(0, 0, 0, 0.2);
  }
}

.rwdpDayBoxDesktop .rwdp-DayCircle-container {
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: default;
  outline: none;
}

.rwdpDayBoxDesktop .rwdp-DayCircle-selected {
  background-color: transparent;
  outline: none;
}

.rwdpDayBoxDesktop .rwdp-DayCircle {
  border-color: blueviolet;
  outline: none;
}

.rwdpDayBoxDesktop .rwdp-DayCircle > strong,
.rwdpDayBoxDesktop .rwdp-DayCircle > div:first-of-type {
  color: blueviolet;
}

.rwdpDayBoxDesktop .rwdp-DayCircle-unavailable,
.rwdpDayBoxDesktop .rwdp-DayCircle-unavailable > strong,
.rwdpDayBoxDesktop .rwdp-DayCircle-unavailable > div:first-of-type {
  border-color: red;
  color: red;
}

.rwdpPrevWeekArrow,
.rwdpNextWeekArrow {
  display: none;
}

.rwdpDayBoxDesktop .rwdp-DayCircle-today {
  font-weight: bold;
}

.rwdpText__Wrapper {
  padding: 0 30px;
  position: relative;
  display: block;
}

.rwdpText__Wrapper > div:first-of-type {
  display: flex;
  justify-content: space-between;
}

.rwdpText__Wrapper > div > div {
  padding: 1px 6px;
  width: 80px;
  min-width: 80px;
  position: relative;
  border: 2px solid transparent;
}

.faceEmoji__Wrapper > img,
.faceEmoji__Wrapper > div > img {
  width: 40%;
}

.faceEmoji__Wrapper > div > div {
  font-size: 0.4em;
}

.News__Wrapper {
  height: 90vh;
  border-radius: 20px;
  /* border: 3px solid white; */
  border: 3px solid var(--point-color);
  background-color: #f7f7fe;
  padding: 2%;
}
.News-box {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.News-Content {
  text-align: start;
  cursor: pointer;
  padding-bottom: 5%;
}

.News-title {
  font-weight: bold;
}

.News-pubDate,
.News-description {
  padding-left: 15px;
}

.News-description {
  font-size: 0.8rem;
}

.News-pubDate {
  color: cadetblue;
  font-size: 0.6rem;
}

.Page__Wrapper {
  margin: 0;
  text-align: center;
  padding: 0;
  display: flex;
  height: 100vh;
}

.LeftSide,
.RightSide {
  padding-top: 3%;
}

.LeftSide {
  width: 60%;
  margin-left: 3%;
}
.RightSide {
  width: 35%;
  margin-right: 3%;
}

